
.folat_menu {
  width: 30px;
  height: 30px;
  background: #000;
  position: fixed;
  top: 90%;
  left: 90%;
}
.root_content {
  max-width: 800px;
  margin: 0 auto;
}
.hp_content {
  max-width:800px;
  min-height:60px;
  line-height: 50px;
  text-align: center;
  text-align: center;
  vertical-align: middle;
  margin: 0 auto;
}
.card {
  position: relative;
  float: left;
  width: 100%;
  margin-right: 10px;
  background: #fafafa;
  border-radius: 10px;
  margin-top: 26px;
  margin-bottom: 26px;
  box-shadow: 3px 3px 7px rgba(0,0,0,0,3);
}
.span_content {
  display: inline-block;
  vertical-align: middle;
  line-height: normal;
  margin-top: 12px;
  padding: 10px 36px 10px 36px;
}
.span_title {
  float: left;
  margin-left: 40px;
  margin-top: 20px;
}
.span_make_time {
  float: right;
  margin-top: 20px;
  margin-right: 40px;
}
.auth {
  float:right;
  padding-bottom: 26px;
  margin-right: 40px;
  margin-top: 5px;
}
ul {
  float: left;
}
img {
  display: block;
  max-width:800px;
  max-height:600px;
  width: 100%;
  height: auto;
  margin: 0 auto;
  border-radius: 10px 10px 0 0;
}
ul {
  list-style:none;
  padding-left:0;
}




